import React, { Component } from 'react';
import {Route, BrowserRouter, Switch} from 'react-router-dom'
import Header from './components/layout/Header'
//import Footer from './components/layout/Footer'
import Home from './components/pages/Home'
import Account from './components/pages/Account'
import SinglePost from './components/pages/SinglePost'
import Author from './components/pages/Author'
import NotFound from './components/pages/NotFound'
import Login from './components/auth/Login'
import Register from './components/auth/Register'
import jwt from 'jsonwebtoken'
import {connect} from 'react-redux'
import * as actions from './actions/authAction'
import axios from 'axios'


class App extends Component {

    componentWillMount() {
        try{
            // darom uzklausa i LS
            const token = localStorage.getItem('token');
            // paimam info is token (ten bus su Bearer ir tarpu, tai reikia splitint)
            if(!token) return;
            // jei turim token,irasom ji i headers axios biblioteka
            axios.defaults.headers.common['Authorization'] = token;
            const user = jwt.decode(token.split(' ')[1]);
            console.log(user);
            this.props.setUser(user)
        } catch (err){
            console.log(err);
        }
    }
  render() {
    return (
      <BrowserRouter>
        <div>
            <Header/>
            <Switch>
                <Route exact path='/' component={Home}/>
                <Route exact path='/login' component={Login}/>
                <Route path='/account' component={Account}/>
                <Route exact path='/register' component={Register}/>
                <Route exact path='/post/:title' component={SinglePost}/>
                <Route exact path='/author/:id' component={Author}/>
                <Route component={NotFound}/>
            </Switch>
        </div>

      </BrowserRouter>
    );
  }
}

export default connect(null,actions)(App);



